@import '../common/styles/sass/variables';
.mainBlock {
    height: 100vh;
    background-color: $bgFirstColor;
    .textBlock {
        width: 500px;
        height: max-content;
        min-height: 150px;
        background-color:$bgSecondColor;
        display: flex;
        flex-direction: column;
        justify-content: center;
        letter-spacing: 1px;
        line-height: 22pt;
        h1,span,p{
            background-color: $bgSecondColor;
            color: $textSecondColor;
        }
        .title h1{
            font-family: $fontMainTitle;
            font-weight: bold;
            font-size: 12px;

        }
    }
    .photoBlock {
        width: 300px;
        height: 70vh;
        background-color: $bgSecondColor;
    }
}

